import React from "react"

function processContentMedia(content) {
  if (!content.includes("\"/uploads/")) {
    return content;
  }

  const prefix = process.env.STRAPI_API_URL || "http://localhost:1337";
  return content.replace(/\/uploads\//g, `${prefix}/uploads/`);
  
}

const BlockRichText = ({
  className = "",
  data,
  ...rest
}) => {
  const content = data.richTextBody ? 
  data.richTextBody.data.childMarkdownRemark.html :
  data;

  const __html = processContentMedia(content); 

  return (
    <div
      dangerouslySetInnerHTML={{
        __html,
      }}
      className={`rich-text ${className}`}
      {...rest}
    />
  )
}

export default BlockRichText
