import React from "react"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"
import isBrowser from "../utils/isBrowser"

const Seo = ({ seo = {} }) => {
  const { strapiGlobal } = useStaticQuery(graphql`
    query {
      strapiGlobal {
        siteName
        favicon {
          localFile {
            url
          }
        }
        defaultSeo {
          metaTitle
          metaDescription
          keywords
          metaRobots
        }
      }
    }
  `)

  const { siteName, defaultSeo, favicon } = strapiGlobal

  // Merge default and page-specific SEO values
  const fullSeo = { ...defaultSeo, ...seo }

  // Add site name to title
  fullSeo.metaTitle = `${fullSeo.metaTitle} | ${siteName}`

  const getMetaTags = () => {
    const tags = []

    const {
      keywords,
      metaDescription,
      metaImage,
      metaTitle,
      metaSocial,
      metaRobots,
      type,
    } = fullSeo;

    const hasMetaSocial = metaSocial && metaSocial.length > 0
    const getMetaSocial = (source) => {
      if (!hasMetaSocial) {
        return {}
      }

      return metaSocial.find(({ socialNetwork }) => socialNetwork === source)
    }

    const facebookMeta = getMetaSocial('Facebook')
    const twitterMeta = getMetaSocial('Twitter')


    tags.push(
      {
        property: "og:title",
        content: facebookMeta.title || metaTitle,
      },
      {
        name: "twitter:title",
        content: twitterMeta.title || metaTitle,
      },
    )

    if (metaDescription) {
      tags.push(
        {
          name: "description",
          content: metaDescription,
        },
        {
          property: "og:description",
          content: facebookMeta.description || metaDescription,
        },
        {
          name: "twitter:description",
          content: twitterMeta.description || metaDescription,
        }
      )
    }
    if (metaImage) {
      let baseUrl = "";
      if (isBrowser()) {
        baseUrl = window.location.origin
      }
      const { og, twitter } = metaImage.localFile.childImageSharp
      tags.push(
        {
          property: "og:image",
          content: `${baseUrl}${og.src}`,
        },
        {
          name: "twitter:image",
          content: `${baseUrl}${twitter.src}`,
        },
      )
    }
    if (type) {
      tags.push({
        property: "og:type",
        content: type,
      })
    }
    if (keywords) {
      tags.push({ keywords })
    }
    tags.push(
      {
        name: "twitter:card",
        content: "summary_large_image"
      },
      {
        name: 'viewport',
        content: "width=device-width, initial-scale=1.0"
      },
    )

    if (metaRobots) {
      tags.push({
        name: 'robots',
        content: metaRobots,
      })
    }

    return tags
  }

  const metaTags = getMetaTags()

  return (
    <Helmet
      title={fullSeo.metaTitle}
      link={[
        {
          rel: "icon",
          href: favicon.localFile.url,
        },
      ]}
      meta={metaTags}
    >
      { fullSeo.structuredData && (
        <script type="application/ld+json">
          {fullSeo.structuredData.internal.content}
        </script>
      )}
    </Helmet>
  )
}

export default Seo
